import axios from 'axios';
import router from '../router'
const auth = {
    signIn:async (Name,Password) =>{
        try{
            var res = await axios.post("Login/login",{
                Name,
                Password
            });
            return res;
        }catch{

        }
    },
    checkAPIResponse: res => {
        if(res.status == 403)
            router.push("/logout");
    },
    getHeader:() =>({ headers: {userToken:localStorage.token}})
};  

export default auth