<template>
  <div class="login">
    <!-- <div class="container" id="container"> -->
    <div class="container" ref="div">
      <!-- <div class="form-container sign-up-container">
				<form action="#">
					<h1>Create Account</h1>
					<div class="social-container">
						<a href="#" class="social"><i class="fab fa-facebook-f"></i></a>
						<a href="#" class="social"><i class="fab fa-google-plus-g"></i></a>
						<a href="#" class="social"><i class="fab fa-linkedin-in"></i></a>
					</div>
					<span>or use your email for registration</span>
					<input t	ype="text" placeholder="Name" />
					<input type="email" placeholder="Email" />
					<input type="password" placeholder="Password" />
					<button>Sign Up</button>
				</form>
      </div>-->
      <div class="form-container sign-in-container">
        <form @submit.prevent="signIn" action="#">
          <!-- <h1>Sign in</h1> -->
          <!-- <div class="social-container">
						<a href="#" class="social"><i class="fab fa-facebook-f"></i></a>
						<a href="#" class="social"><i class="fab fa-google-plus-g"></i></a>
						<a href="#" class="social"><i class="fab fa-linkedin-in"></i></a>
          </div>-->
          <!-- <span>or use your account</span> -->
          <input v-model="username" placeholder="Username or Email" />
          <input v-model="psw" type="password" placeholder="Password" />
          <small v-if="errorMessage">{{errorMessage}}</small>
          <!-- <a href="#">Forgot your password?</a> -->
          <button :disabled="loading">{{loading ? 'Signing In ...':'Sign In'}}</button>
        </form>
      </div>
      <div class="overlay-container">
        <div class="overlay">
          <div class="overlay-panel overlay-right">
            <h1>Welcome Back!</h1>
            <p>To keep connected with us please login with your personal info</p>
            <!-- <button class="ghost" id="signIn">Sign In</button> -->
            <!-- <button class="ghost" @click="signIn">Sign In</button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import auth from "@/services/auth";
// import {signalR} from "@/services/signalR";
export default {
    data() {
        return {
            username: '',
            psw: '',
            loading : false,
            errorMessage : ''
        }
    },
    computed:{
      jqueryObject(){
        return window.$;
      }
    },
	methods:{
		signIn: async function() {	
            this.errorMessage = '';
            if(this.username && this.psw){
                this.loading = true;
                try{
                    var res = await axios.post("login",{
                        Username: this.username,
                        Password: this.psw
                    });
                    if(res.status == 200){
                        this.errorMessage = 'Success. Redirecting to Home Page.';
                        let info = localStorage.token = res.data.token;
                        info = JSON.parse(atob(info));
                        this.redirect(info);
                    }
                    else if(res.status == 204)
                        this.errorMessage = 'User not found!';
                }
                catch{
                    this.errorMessage = 'Could not connect to server!';
                }
                this.loading = false;
            }
            else
                this.errorMessage = "Please fill required fields";
			//alert('signIn') 
			//let container = document.getElementById("container");
			//console.log(container)
			//container.classList.remove("right-panel-active");
			// this.$refs.div.classList.remove("right-panel-active");
            // if(this.username && this.psw){
            //     var res = await axios.post("Login/login",{
            //         admin:{
            //             Name: this.username,
            //             Password: this.psw
            //         }
            //     });
            //     console.log('====================================');
            //     console.log(res);
            //     console.log('====================================');
            // }
		},
		signUp: function() {
			//alert('signUp')
			//let container = document.getElementsByClassName('container');
			//let container = document.getElementById("container");
			//console.log(container)
			//container.classList.add("right-panel-active");
			this.$refs.div.classList.add("right-panel-active");
			
    },
    redirect ({role}){
      if(role == "Admin" || role == "Team")
        this.$router.push("/dashboard");
      else
        this.$router.push("/conversations");
    }
  },
  mounted(){

    // signalR.registerClient();
    // signalR.start().then();
  }
}
</script>

<style lang="scss" scoped>

	@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');
    @import "../../node_modules/bootstrap/scss/bootstrap.scss";
	.login {
		background: url(https://images.unsplash.com/photo-1574092403000-ef9c803b84bb?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2000&q=100);
		background-size: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		font-family: 'Montserrat', sans-serif;
		height: 100vh;
		/*margin: -20px 0 50px;*/		
	}

h1 {
  font-weight: bold;
  margin: 0;
}

h2 {
  text-align: center;
}

p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

span {
  font-size: 12px;
}

a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

button {
  border-radius: 20px;
  border: 1px solid $primary;
  background-color: $primary;
  color: #ffffff;
  font-size: 16px;
  padding: 8px 45px;
  letter-spacing: 1px;
  transition: transform 80ms ease-in;
  margin-top: 10px;
}

button:active {
  transform: scale(0.95);
}

button:focus {
  outline: none;
}

button.ghost {
  background-color: transparent;
  border-color: #ffffff;
}
form {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}
input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

.container {
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.overlay {
  background: #8e2de2; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #4a00e0,
    #8e2de2
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #4a00e0,
    #8e2de2
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}
</style>