import Vue from 'vue'
import Vuex from 'vuex'
import auth from "@/services/auth";
import {
  stat
} from 'fs';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    status: false,
    FBname: '',
    chatList: [],
    otherChannels: [],
    pageLoaded: false,
    selectedCustomer: {},
    chatList: [],
    messages: [],
    msgBinder: '',
    customers: [],
    newCustomers: [],
    newCustomer: {},
    msgTempleate: {
      Agent: null,
      AgentID: null,
      Attachments: "{\"data\":[]}",
      Status: "Sending",
      Type: "Replied",
    },
    selectedThread: {},
    selectedChannel: 2,
    inputmsg: "",
    threadObj: {},
    finishedCustomer: {},
    pendingOrFinished: "pending",
    channel: {},
    replycount: 0,
    toreplycount: 0,
    followupcount: 0,
    all: 0,
    condition: "Inbox",
    statusPie: "To Reply",
    channelPie: "",
    agentBar: "",
    searchTerm: "",
    toggle: false,
    statusFilter: "",
    channelFilter: "",
    agentFilter: "",
    keywords: "",
    filterState: "",
    filterArr: []
  },
  mutations: {
    updateSource(state, sourceObj) {
      state.status = sourceObj.status;
      state.FBname = sourceObj.FBName;
      //console.log("index.js ="+ state.status);
      //console.log("index.js ="+ state.FBname);
    },
    updateFont(state, font) {
      state.selectedCustomer.MyanFont = font;
      let index = state.chatList.findIndex(x => x.MsgrID == state.selectedCustomer.MsgrID);
      state.chatList[index].MyanFont = font;

    },
    updateSelectedChat(state, payload) {
      state.selectedChat = payload;
    },
    //
    updatePageLoaded(state, payload) {
      state.pageLoaded = payload;
    },
    newCustomerToChatList(state, customer) {
      state.chatList.push(customer);
    },

    updateChatList(state, data) {
      state.chatList = data;
    },
    updatePendingOrFinished(state, data) {
      state.pendingOrFinished = data;

    },
    addNewCustomer(state, c) {
      state.newCustomers.push(c);
    },
    // updateSelectedCustomerThread(state,data){
    //   let threadID=state.selectedThread.ID;
    //   state.selectedCustomer.ThreadID=threadID;
    //   Vue.set(state.selectedCustomer, "ThreadID",threadID);
    // },
    updateFinished(state, data) {
      state.finishedCustomer = data;

    },
    updateSelectedThread(state, data) {
      state.selectedThread = data;
    },
    updateThread(state, data) {
      state.threadObj = data;
    },
    appendChatList(state, payload) {
      state.chatList.push(payload.customer);
      this.commit("sortChatList", payload.order);
    },
    updateSelectedCustomer(state, customer) {
      Vue.set(state, "selectedCustomer", customer);
    },
    updateMessages(state, messages) {
      Vue.set(state, "messages", messages);
    },
    updateChannel(state, channel) {
      state.channel = channel;
    },
    updateMessage(state, msg) {
      state.messages = state.messages.map(x => {
        if (x.ID == msg.ID || (x.unique && x.unique === msg.unique)) {
          x = msg;
        }
        return x;
      })
    },
    updateSelectCustomer(state) {
      state.selectedCustomer.IsReplied = true;
    },
    updateFlagged(state) {
      if (state.selectedCustomer.Flagged == true) {
        state.followupcount++;
      } else {
        state.followupcount--;
      }
      state.toggle = false;
    },
    updateFilterArr(state, fil) {
      let exits = state.filterArr.find(x => x === fil);
      if (state.filterArr.length < 3 && !exits) {
        state.filterArr.push(fil);
      }
    },
    removeFilterArr(state, fil) {
      if (fil == "status") {
        state.statusFilter = "";
      } else if (fil == "channel") {
        state.channelFilter = "";
      } else {
        state.agentFilter = "";
      }
      let i = state.filterArr.findIndex(x => x == fil);
      state.filterArr.splice(i, 1);
    },

    updateMsgStatus(state, payload) {
      let i = state.messages.findIndex(x => x.unique === payload.unique);
      state.messages[i].Status = "Replied";
      //   state.messages[i] = payload.message;

    },

    updateCustomer(state, obj) {
      console.log(obj);
      let customerExists = state.chatList.find(x => x.ID === obj.ID);
      if (customerExists) {
        if (state.condition == "Replied" && obj.IsReplied == false && state.toggle == false) {
          state.condition = "Inbox";
          state.toreplycount++;
          state.replycount--;
        } else if (state.condition == "Inbox" && obj.IsReplied == true && state.toggle == false) {
          state.condition = "Replied";
          state.toreplycount--;
          state.replycount++;
        }
        state.chatList = state.chatList.map(x => {
          if (x.ID == obj.ID) {
            x = {
              ...x,
              ...obj
            };
          }
          return x;
        });
      } else {
        state.chatList.push(obj);
        if (state.toggle == false && obj.agent != null) {
          state.toreplycount++;
        }
        if (obj.Flagged == true) {
          state.followupcount++;
        }
        if (state.toggle == false && obj.IsReplied == true) {
          state.replycount++;
          state.all++;
        } else if (state.toggle == false && obj.IsReplied == false) {
          state.toreplycount++;
          state.all++;
        }
        if (state.filter == "Inbox") {
          state.chatList.sort(
            (a, b) => new Date(a.UpdatedAt) - new Date(b.UpdatedAt)
          );
        } else {
          state.chatList.sort(
            (a, b) => new Date(b.UpdatedAt) - new Date(a.UpdatedAt)
          );
        }
      }
    },

    updateLabel(state, label) {
      Vue.set(state, "messages",
        state.messages.map(x => {
          if (x.ID === label.MessageID)
            x.Label = `${label.ToC} | ${label.Category} | ${label.Product}`;
          return x;
        }));
    },
    removeLabel(state, id) {
      Vue.set(state, "messages",
        state.messages.map(x => {
          if (x.ID === id)
            x.Label = "";
          return x;
        }));
    },
    removeNewCustomer(state, id) {
      state.newCustomers = state.newCustomers.filter(x => x.ID != id);
    },
    pushAsSending(state, obj) {
      let msg = {
        ...state.msgTempleate,
        ...obj
      };
      state.messages = [...state.messages, msg];
    },
    msgSendingFailed(state, unique) {
      let i = state.messages.findIndex(x => x.unique == unique);
      Vue.set(state.messages[i], "Status", "Fail");
    },
    addMessage(state, msg) {
      if (state.selectedCustomer.ID === msg.CustomerID)
        state.messages.push(msg);
    },
    newCustomer(state, customer) {
      let exists = state.newCustomers.includes(x => x.ID === customer.ID);
      if (!exists)
        state.newCustomers.push(customer);
    },
    removeAssignedCustomer(state, payload) {
      if (payload.adminId !== state.env.adminId) {
        state.chatList = state.chatList.filter(x => x.ID !== payload.customerId);
        if (state.selectedCustomer.ID === payload.customerId)
          this.updateSelectedCustomer({});
      } else {
        let i = state.chatList.findIndex(x => x.ID === payload.customerId);
        let customer = state.chatList[i];
        customer.ActiveAgentID = payload.adminId;
        state.chatList.splice(i, 1, customer);
      }
    },
    removeCustomer(state, customerId) {
      state.chatList = state.chatList.filter(x => x.ID != customerId);
    },
    removeNewCustomer(state, customerId) {
      state.newCustomers = state.newCustomers.filter(x => x.ID != customerId);
    },
    sortChatList(state, order) {
      if (order == "ASC")
        state.chatList = state.chatList.sort((a, b) => {
          if (a.UpdatedAt < b.UpdatedAt) {
            return -1;
          }
          if (a.UpdatedAt > b.UpdatedAt) {
            return 1;
          }
          return 0;
        });
      else
        state.chatList = state.chatList.sort((b, a) => {
          if (a.UpdatedAt < b.UpdatedAt) {
            return -1;
          }
          if (a.UpdatedAt > b.UpdatedAt) {
            return 1;
          }
          return 0;
        });
    }
  },
  actions: {
    // async getAdminData({ state }) {
    //   let url = state.env.apiUrl + "/getAdminData";
    //   return await axios.get(url,auth.getHeader());
    // },
    async getChatList({
      commit,
      state
    }, p) {
      let url = state.env.apiUrl + "/api/customer/list";
      url += p.pending ? '' : '/finished';
      return await axios.post(url, {
        ChannelId: state.env.channelId,
        AdminId: state.env.adminId,
        filters: p.filters
      });
    },
    async getChatListWithFilter({
      commit,
      state
    }, p) {
      // console.log('call')
      let url = state.env.apiUrl + "/api/customer/list";
      url += p.pending ? '/pending' : '/finished';
      return await axios.post(url, p);
    },
    async getConversation({
      state
    }, assign) {
      let url = state.env.apiUrl + "/api/conversation";
      url += "/" + state.selectedCustomer.ID;
      url += "/" + state.selectedCustomer.ThreadID;
      url += "?agentId=" + state.env.adminId;
      url += "&assign=" + assign;
      return await axios.get(url);
    },
    async getConversationWithThread({
      state
    }, assign) {
      let url = state.env.apiUrl + "/api/conversation";
      url += "/" + state.finishedCustomer.ID;
      url += "/" + state.selectedThread.ID;

      return await axios.get(url);

    },
    async getShareLog({
      commit,
      state
    }, p) {
      let url = "api/message/getShareLog";
      return await axios.get(url, {
        params: {
          threadid: p
        }
      });
    },
    async getThreadObj({
      commit,
      state
    }, p) {
      let url = state.env.apiUrl + "/api/thread/SolvedThreadList";
      return await axios.get(url, {
        params: {
          CustomerID: p
        }
      });
    },
  },
  getters: {
    selectedCustomer: state => state.selectedCustomer,
    api: state => state.env.apiUrl,
    admin: () => JSON.parse(atob(localStorage.token)),
    channelId: state => state.env.channelId,
    channelName: state => state.env.channelName,
    isAdmin: () => true,
    selectedCustomerID: state => state.selectedCustomer.ID
  }
})
